import * as React from 'react';
import styled from 'styled-components';
import { CommonLine } from '@ali/we-design';
import { Block, RenderNodeProps } from '@ali/4ever-cangjie';
import { theme } from '@ali/4ever-bamboo';
import { MAPPING, parseJsonMLToValue } from '@ali/4ever-utils';
import { moFactory } from '@ali/4ever-plugin-hr';

const { height: themeHeight } = theme.hr;

const HrWrapper = styled.div`
  height: ${themeHeight}px;
  padding: 5px 0;
`;

export default function createHrPlugin() {
  return {
    ...moFactory({}),
    renderNode(props: RenderNodeProps<Block>, _, next) {
      const { node } = props;
      if (node.type !== 'hr') {
        return next();
      }

      const { attributes } = props;
      const payload = node.data || {};
      // @ts-ignore
      const line = <CommonLine {...parseJsonMLToValue(payload, MAPPING)} />;
      return (
        <HrWrapper
          data-type="hr"
          {...attributes}
        >
          {line}
        </HrWrapper>
      );
    },
  };
}
